import React, { Component } from 'react';
import styled from 'styled-components';
import {media} from '../styles/style-utils';
import Item from '../components/Item';
import data from '../data.json';

const ItemHolder = styled.div`
    width: 100%;
    ${media.tablet`
        width: 830px;
    `}
    margin: 0 auto;
`

const ItemRow = styled.div`
    display:grid;
    grid-row-gap: 20px;
    ${media.tablet`
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 20px;
    `}
`

const ItemSection = styled.div`
    padding: 40px 10px;
    ${media.tablet`
        padding: 55px;
    `}
    text-align: left;
`

const ItemCategory = styled.div`
    font-size: 25px;
    font-weight: 700;
    padding-bottom: 38px;
    padding-left: 10px;
    display:inline-block;
    p{
        /* text-align:left; */
    }
    div{
        margin-top: 9px;
        background: rgba(244,189,97,0.85);
        width: calc(100% + 30px);
        height: 6px;
    }
`

class Items extends Component {
    render() {
        return (
            <ItemHolder>
                {data.map(_ => (
                    <ItemSection key={_.category}>
                        <ItemCategory>
                            <p>{_.category}</p>
                            <div></div>
                        </ItemCategory>
                        <ItemRow>
                            {_.items.map(__ => (
                                <Item key={__.name} {...__}></Item>
                            ))}
                        </ItemRow>
                    </ItemSection>
                ))}
            </ItemHolder>
        );
    }
}

export default Items;