import React, { Component } from 'react';
import styled from 'styled-components';
import { RightIcon } from './Icons';

const ItemDiv = styled.div`
    display: inline-block;
    padding: 12px;
    background: #fff;
    box-shadow: 0 2px 4px rgba(0,0,0,.11);
    border-radius: 8px;
    text-align: left;
    p {
        font-size: 12px;
    }
`;

const Logo = styled.div`
    display: inline-block;
    vertical-align: top;
    width: 80px;
    height: 80px;
    line-height: 80px;
    position: relative;
    img{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateY(-50%) translateX(-50%);
        border-radius: 4px;
    } 
`;

const Exp = styled.div`
    display: inline-block;
    vertical-align: top;
    width: calc(100% - 80px);
    padding-left: 15px; 
    p{
        &:first-child{
            font-size: 20px;
            font-weight: 700;
        }
        &:last-child{
            font-size: 13px;
            font-weight: 400;
            padding-top: 5px;
            color: #676767;
        }
    }
`;

const DealText = styled.div`
    width: 90%;
    margin-left: 5%;
    background: rgba(246,249,252, 59%);
    text-align: center;
    text-transform: uppercase;
    color: #3C3C3C;
    letter-spacing: 0.5px;
    font-weight: 700;
    padding: 2px 0 4px 0;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px; 
`

const Bottom = styled.div`
   margin-top: 16px;
`;

const Button = styled.a`
    display: inline-block;
    width: 100%;
    padding: 10px 0 13px 0;
    background: #000;
    border-radius: 8px;
    box-shadow: 0 2px 4px rgba(0,0,0,.11);
    text-align:center;
    color: #fff;
    text-decoration: none;
    font-size: 17px;
    letter-spacing: .5px;
    font-weight: 800;
    transition: 300ms all;
    svg{
        margin-right: 10px;
        transition: 250ms transform;
    }
    &:hover{
        svg{
            transform: translateX(5px)
        }
        box-shadow: 0px 5px 11px rgba(0,0,0,.18);
        transform: translateY(-2px);
    }
`

const ButtonExpired = styled.a`
    display: inline-block;
    width: 100%;
    padding: 10px 0 13px 0;
    border-radius: 8px;
    box-shadow: 0 2px 4px rgba(0,0,0,.11);
    text-align:center;
    color: #fff;
    text-decoration: none;
    font-size: 17px;
    letter-spacing: .5px;
    font-weight: 800;
    transition: 300ms all;
    background: #3b3e468c;
    svg{
        margin-right: 10px;
        transition: 250ms transform;
    }
    &:hover{
        svg{
            transform: translateX(5px)
        }
        box-shadow: 0px 5px 11px rgba(0,0,0,.18);
        transform: translateY(-2px);
    }
`

class Items extends Component {
    render() {
        let button;
        if(this.props.isExpired){
            button = <ButtonExpired href="#0">DEAL EXPIRED</ButtonExpired>
        }else{
            button = <Button href={this.props.link} target="_blank"><RightIcon /> {this.props.buttonText}</Button>;
        }

        return (
            <ItemDiv>
                <div>
                    <Logo>
                        <img src={this.props.logo} width={this.props.logoWidth}/>
                    </Logo>
                    <Exp>
                        <p>{this.props.name}</p>
                        <p>{this.props.p}</p>
                    </Exp>
                </div>
                <Bottom>
                    <DealText>
                        <p>{this.props.dealText}</p>
                    </DealText>
                    {button}
                </Bottom>
            </ItemDiv>
        );
    }
}

export default Items;